/*------------------------------------------*/
/*#HELPER CLASSES*/
/*------------------------------------------*/

/*----------font weight-------------------*/

.fw--light {
  font-weight: 300;
}

.fw--regular {
  font-weight: 400;
}

.fw--medium {
  font-weight: 500;
}

.fw--semibold {
  font-weight: 600;
}

.fw--extrabold {
  font-weight: 800;
}

.fw--black {
  font-weight: 900;
}

/*----------font style------------------*/

.txt--uppercase {
  text-transform: uppercase;
}

.txt--lowercase {
  text-transform: lowercase;
}

.txt--capitalize {
  text-transform: capitalize;
}

.txt--overline {
  text-decoration: overline;
}

.txt--linethrough {
  text-decoration: line-through;
}

.txt--underline {
  text-decoration: underline;
}

/*-----------font align -------------------*/

.txt--center {
  text-align: center;
}

.txt--left {
  text-align: left;
}

.txt--right {
  text-align: right;
}

.txt--justify {
  text-align: justify;
}

/*---------elements align------------------*/

.pull--left {
  float: left;
}

.pull--right {
  float: right;
}

.pull--none {
  float: none;
}

.clearfix {
  clear: both;
}

.clearfix::before,
.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

/*--------elements visibility--------------------*/

.visible {
  visibility: visible;
}

.invisible {
  visibility: hidden;
}

/*--------overflow---------------------------------*/

.overflow--hidden {
  overflow: hidden;
}

.overflow--visible {
  overflow: visible;
}

.overflow--auto {
  overflow: auto;
}

/*--------position---------------------------------*/

.p--relative {
  position: relative;
}

.p--sticky {
  position: sticky;
}

/*----------------------------------------------------------------------------*/
/*#VIDEO*/
/*----------------------------------------------------------------------------*/

.video {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  padding-top: 25px;
  /* if needed */
  height: 0;
}

.video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/*----------------------------------------------------------------------------*/
/*#FONT COLOR*/
/*----------------------------------------------------------------------------*/

.txt--white {
  color: #fff;
}

.txt--black {
  color: #000;
}

/*----------------------------------------------------------------------------*/
/*#CONTAINER STYLE*/
/*----------------------------------------------------------------------------*/

[class*="container"] {
  padding-left: 15px;
  padding-right: 15px;
}

.container {
  max-width: 1650px;
}

.container--lg {}

.container--md {}

.container--sm {}

.container--xs {}

/*----------------------------------------------------------------------------*/
/*#GUTTERS*/
/*----------------------------------------------------------------------------*/

.gutters-xs {
  margin-left: -7.5px;
  margin-right: -7.5px;

  .col,
  [class*="col-"] {
    padding-left: 7.5px;
    padding-right: 7.5px;
  }
}

.gutters-sm {
  margin-left: -10px;
  margin-right: -10px;

  .col,
  [class*="col-"] {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.gutters-md {
  margin-left: -20px;
  margin-right: -20px;

  .col,
  [class*="col-"] {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.gutters-lg {
  margin-left: -30px;
  margin-right: -30px;

  .col,
  [class*="col-"] {
    padding-left: 30px;
    padding-right: 30px;
  }
}

